  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: poppins ;
}

* body {
  width: 100%;
  height: 100%;
}

/* 
 * skip-link style 
 */

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  z-index: 1;
  border: none;
  background-color: #24B2B4;
  color: white;
  border-radius: 5px;
}
* body .skip-link:focus {
  top: 0;
}

/*
 * Top level navigation
 */

 * body header {
  background-color: #F0972E;
  min-height: 70px;
  display: block;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
* body header .header__title {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
  padding: 1rem;
}
* body header .header__burger {
  font-size: 3rem;
  text-align: center;
  margin: 10px auto;
  display: inline-block;
  min-width: 50px;
  color: white;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media screen and (min-width: 603px) {
  * body header .header__burger {
    display: none;
  }
}
* body header nav {
  font-size: larger;
}
* body header nav ul {
  display: flex;
  margin-top: 7.5px;
}
* body header nav ul li {
  padding: 1rem;
  list-style-type: none;
}
* body header nav ul li a {
  padding: 1rem;
  text-decoration: none;
  border: none;
  color: white;
  padding: 1rem;
}
* body header #drawer ul {
  list-style-type: none;
}

/*
 * Jumbotron
 */

.hero {
  width: 100%;
  height: 80vh;
  position: relative;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero__title {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: #ffffff;
  background: rgba(0, 0, 0, 0.444);
}
.hero__tagline {
  position: absolute;
  text-align: center;
  cursor: pointer;
  color: white;
  height: 50px;
  font-size: 15px;
  bottom: 150px;
}
@media screen and (max-width: 768px) {
  .hero {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
  .hero__title, .hero__tagline {
    padding: 10px;
  }
}
@media screen and (max-width: 992px) {
  .hero__title, .hero__tagline {
    padding: 5px;
  }
}
@media screen and (min-width: 576px) {
  .hero__title {
    font-size: 2rem;
  }
}
@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 2rem;
  }
}
@media screen and (min-width: 992px) {
  .hero__title {
    font-size: 2.5rem;
  }
}
.hero__title p {
  font-size: inherit;
  color: inherit;
  width: 300px;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .hero__title p {
    width: 650px;
  }
}

/*
 * Content
 */

* body main {
  width: 100%;
}
* body main .content{
  padding: 32px;
  background-color: #FBF9F1;
}
* body .explore {
  text-align: center;
  font-weight: 800;
  font-size: 1.2rem;
  line-height: 9rem;
  padding-top: 2em;
  color: #2D3E4A;
}
* body main .cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  align-items: center;
  justify-items: center;
  gap: 2em;
  padding-top: 4em;
}
@media screen and (max-width: 576px) {
  * body main .cards {
    margin: auto;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

/*
 * cards item
 */

* body main .cards .card-item {
  background-color: #FBF9F1;
  border: none;
  border-radius: 6px;
  box-shadow:
    0 4px 8px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 100%;
  height: 100%;
  padding: 1em;
}
* body main .cards .card-item:hover {
  transform: translateY(-8px);
}
* body main .cards .card-item_image {
  width: 100%;
  padding-top: 56.25%;
  overflow: hidden;
  position: relative;
  border: none;
  border-radius: 5px;
}
* body main .cards .card-item_image img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
* body main .cards .card-item_location {
  position: absolute;
  top: 12px;
  background-color: #cecece;
  opacity: 0.8;
  padding: 0.5em;
  border: none;
  border-radius: 0 5px 5px 0;
  font-weight: 600;
  font-size: 1em;
}
* body main .cards .card-item_rating {
  position: absolute;
  bottom: 2px;
  right: 2px;
  padding: 1em;
  border: none;
  border-radius: 100%;
  background-color: #24B2B4;
  font-weight: 600;
  color: #fff;
  font-size: 1em;
}
* body main .cards .card-item_content {
  padding: 10px;
}
* body main .cards .card-item_title {
  margin-bottom: 10px;
  font-size: 1.3em;
  font-weight: 700;
  text-align: center;
}
* body main .cards .card-item_title a {
  color: #24B2B4;
  padding: 1rem;
}
* body main .cards .card-item_title a:hover {
  color: #F0972E;
}
* body main .cards .card-item_desc {
  font-size: 0.8em;
  text-align: justify;
}
* body main .card-item .card-item_image {
  width: 100%;
  overflow: hidden;
  position: relative;
  border: none;
  border-radius: 5px;
}

/* Resto Detail */

.resto-menu, .resto-review, .resto-info  {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  overflow: hidden;
}

.resto-detail {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  overflow: hidden;
}

.resto-image-detail {
  width: 100%;
  border-radius: 10px;
}

.resto-title-detail {
  font-weight: bold;
  margin: 16px;
  color: #F0972E;
}

.resto-description-detail {
  margin: 16px;
  font-size: 1.2em;
  line-height: 1.5em;
  overflow: hidden;
}

.resto-label{
  font-weight: 500;
  margin: 16px;
  color: #F0972E;
}

.resto-text {
  font-weight: lighter;
  margin: 16px;
}

.resto-menu div > ul {
  padding: 18px;
  border-radius: 25px;
  background-color: #f1f1f1;
  margin-top: 10px;
  margin-right: 10px;
}
.resto-menu div > ul > li {
  margin-left: 10px;
  margin-bottom: 15px;
  font-size: 1rem;
  font-weight: 700;
  list-style-type: none;
}
.resto-menu div > ul > ul {
  font-size: 0.9rem;
  columns: 2;
}
.resto-menu div > ul > ul > li {
  margin-left: 28px;
  padding: 4px;
}

.review{
  display: block;
  padding: 0 25px;
  margin: 16px 0 0 0;
  border-radius: 25px;
  background-color: #f1f1f1;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;  
}

/*
 * Footer
 */

* body footer {
  margin-top: 20px;
  padding: 15px;
  width: 100%;
  color: rgb(100 116 139);
  background-color: #2D3E4A;
  text-align: center;
  font-weight: 500;
  font-size: 0.8rem;
}
