___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 603px) {
    header {
        display: flex;
        flex-direction: row;
        min-width: 100%;
    }
    header h1{
        margin: 0 auto;
        font-size: 1.5 rem;
    }
    header .header__burger {
        margin: 0 auto;
        font-size: 30px;
        color: white;
    }
    header .nav {
        width: 100%;
        background-color: #F0972E;
        z-index: 10;
        margin-top: 110px;
        left: -0.01px;
        position: absolute;
        transform: translate(-900px, 0px);
    }
    header .nav .nav__list {
      display: flex;
      flex-direction: column;
    }
    header .nav .nav__list .nav__item {
      display: list-item;
      width: 100%;
      text-align: left;
    }
    header #drawer.open {
      transform: translate(0, -18px);
    }
    main img {
      width: 100vw;
    }
  }

  @media screen and (max-width: 499px) {
    .review {
        display: grid;
        grid-template-columns: 1fr;
        height: 175px;
        font-size: 14px;
        line-height: 50px;
    }

    .review .name-review{
        font-weight: 700;
    }
    .review .description-review{
        font-weight: 400;
    }
    .review .date-review{
        font-size: 0.8em;
    }
  }

@media screen and (min-width: 500px) {
    .review {
        display: grid;
        grid-template-columns: 1fr;
        height: 175px;
        font-size: 14px;
        line-height: 50px;
    }

    .review .name-review{
        font-weight: 700;
    }
    .review .description-review{
        font-weight: 400;
    }
    .review .date-review{
        font-size: 0.8em;
    }
  }

  @media screen and (min-width: 650px) {
    .review {
        display: grid;
        grid-template-columns: 1fr;
        height: 175px;
        font-size: 14px;
        line-height: 50px;
    }

    .resto-menu div > ul > ul {
        columns: 3;
    }
  }

@media screen and (min-width: 700px) {
    .review {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        height: 50px;
        font-size: 14px;
        line-height: 50px;
    }

    .resto-menu div > ul > ul {
        columns: 3;
    }
  }

@media screen and (min-width: 800px) {
    .review {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        height: 50px;
        font-size: 14px;
        line-height: 50px;
    }
  }

@media screen and (min-width: 900px) {
    .detail {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
    }

    .resto-menu, .resto-review {
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .review {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        height: 50px;
        font-size: 14px;
        line-height: 50px;
    }

    .resto-menu div > ul > ul {
        columns: 4;
    }
  }

@media screen and (min-width: 1200px) {
    .detail {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 10px;
    }
    .resto-menu, .resto-review {
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .review {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        height: 50px;
        font-size: 14px;
        line-height: 50px;
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI;QACI,aAAa;QACb,mBAAmB;QACnB,eAAe;IACnB;IACA;QACI,cAAc;QACd,kBAAkB;IACtB;IACA;QACI,cAAc;QACd,eAAe;QACf,YAAY;IAChB;IACA;QACI,WAAW;QACX,yBAAyB;QACzB,WAAW;QACX,iBAAiB;QACjB,aAAa;QACb,kBAAkB;QAClB,iCAAiC;IACrC;IACA;MACE,aAAa;MACb,sBAAsB;IACxB;IACA;MACE,kBAAkB;MAClB,WAAW;MACX,gBAAgB;IAClB;IACA;MACE,8BAA8B;IAChC;IACA;MACE,YAAY;IACd;EACF;;EAEA;IACE;QACI,aAAa;QACb,0BAA0B;QAC1B,aAAa;QACb,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,gBAAgB;IACpB;IACA;QACI,gBAAgB;IACpB;IACA;QACI,gBAAgB;IACpB;EACF;;AAEF;IACI;QACI,aAAa;QACb,0BAA0B;QAC1B,aAAa;QACb,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,gBAAgB;IACpB;IACA;QACI,gBAAgB;IACpB;IACA;QACI,gBAAgB;IACpB;EACF;;EAEA;IACE;QACI,aAAa;QACb,0BAA0B;QAC1B,aAAa;QACb,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,UAAU;IACd;EACF;;AAEF;IACI;QACI,aAAa;QACb,kCAAkC;QAClC,YAAY;QACZ,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,UAAU;IACd;EACF;;AAEF;IACI;QACI,aAAa;QACb,kCAAkC;QAClC,YAAY;QACZ,eAAe;QACf,iBAAiB;IACrB;EACF;;AAEF;IACI;QACI,aAAa;QACb,8BAA8B;QAC9B,cAAc;IAClB;;IAEA;QACI,oBAAoB;QACpB,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,kCAAkC;QAClC,YAAY;QACZ,eAAe;QACf,iBAAiB;IACrB;;IAEA;QACI,UAAU;IACd;EACF;;AAEF;IACI;QACI,aAAa;QACb,8BAA8B;QAC9B,cAAc;IAClB;IACA;QACI,oBAAoB;QACpB,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,kCAAkC;QAClC,YAAY;QACZ,eAAe;QACf,iBAAiB;IACrB;EACF","sourcesContent":["@media screen and (max-width: 603px) {\r\n    header {\r\n        display: flex;\r\n        flex-direction: row;\r\n        min-width: 100%;\r\n    }\r\n    header h1{\r\n        margin: 0 auto;\r\n        font-size: 1.5 rem;\r\n    }\r\n    header .header__burger {\r\n        margin: 0 auto;\r\n        font-size: 30px;\r\n        color: white;\r\n    }\r\n    header .nav {\r\n        width: 100%;\r\n        background-color: #F0972E;\r\n        z-index: 10;\r\n        margin-top: 110px;\r\n        left: -0.01px;\r\n        position: absolute;\r\n        transform: translate(-900px, 0px);\r\n    }\r\n    header .nav .nav__list {